import styles from "./popups.module.css"

type TOrderDetailsProps = {
  orderNumber: number
}

export const OrderDetails = (props: TOrderDetailsProps) => {
  return (
    <div className={styles.wrapper}>
      <p className="text text_type_digits-large mt-20 mb-8">{props.orderNumber}</p>
      <h2 className="text text_type_main-medium pb-15"> </h2>
      <div className={`${styles.checkmark} mb-15`}></div>
      <p className="text text_type_main-default mb-2">   </p>
      <p className="text text_type_main-default text_color_inactive pb-20">
            
      </p>
    </div>
  )
}
